#navbar-right-searchbar {
  margin-right: 40px;
  float:right;
  width: 30%;
  min-width: 200px;
  border-radius: 2px;
  padding: 5px 10px;
  height: 25px;
  font-size: 16px;
  border-style:none;
}
#navbar-right-searchbar:focus {
  outline: none;
}
.hh-colorlist {
  text-align: center;
  padding-top: 50px;
  display: inline-block;
  background-color: rgb(199, 202, 201);
  width: 25%;
  border-right: rgb(189, 188, 188) solid 3px;
  height: calc(100vh - 110px);
  box-shadow: 2px 1px rgb(228, 228, 228);
}
.hh-colorlist > ul {
  padding: 0;
  margin:0 auto;
  width: 80%;
  list-style: none;
}
.hh-colorlist > ul > #hh-colorlist-randomize {
  border: 1px solid black;
  font-family:'Source Serif Pro';
  font-size: 16px;
  height: 40px;
  border-radius: 5px;
  width: 100%;
  cursor: pointer;
  margin-bottom: 15px;
}
.hh-colorlist > ul > #hh-colorlist-randomize:focus {
  outline:none;
}
.hh-colorlist > ul > #hh-colorlist-randomize:active {
  width: 90%;
  background-color:#363B3C;
  color:white;
}
.hh-colorlist > ul > li {
  cursor: pointer;
  margin-top: 15px;
  text-align: left;
  font-size: 20px;
}
.hh-colorlist > ul > li:active {
  font-size: 22px;
}
@media screen and (max-width: 649px) {
  .hh-colorlist {
    padding-top: 5px;
    width: 100%;
    height: auto;
  }
  .hh-colorlist > ul > li {
    display: inline-block;
    margin: 5px 13px;
  }
  .hh-colorlist > ul > li:active {
    font-size: 16px;
  }

}

.hh-colorboard {
  vertical-align: top;
  padding-top: 50px;
  display: inline-block;
  width: calc(75% - 3px);
  text-align: center;
  height: calc(100vh - 110px);
  overflow-y: auto;
}
@media screen and (max-width: 649px) {
  .hh-colorboard {
    padding-top: 5px;
    width: 100%;
    height: auto;
  }
}
.hh-colorboard-swatch {
  cursor: pointer;
  display: inline-block;
  margin: 10px;
  min-width: 125px;
  width: 20%;
  height: 145px;
  position: relative;
  border-radius: 5px;
}
.hh-colorboard-swatch:hover {
  width: 22%;
}
.hh-colorboard-swatch-desc {
  width: 100%;
  min-height: 40px;
  height: auto;
  position: absolute;
  bottom: 0;
  background-color: rgb(238, 238, 238);
  line-height: 2.5;
  border-radius: 4px;
}

@media screen and (max-width: 649px) {
  .hh-colorboard-swatch {
    margin: 3px;
  }
}
